// show Data

const showData = () => {
    if (localStorage.getItem("form") !== null) {
        dataLists = JSON.parse(localStorage.getItem("form"));
    }

    let markupElement = "";
    // console.log(dataLists);
    dataLists.forEach((data, index) => {
        markupElement += `
        <li class="main__section_card">
            <div class="card_detail">
            <p>ID: <small>${index} </small></p>
            <p>Name: <small>${data.name} </small></p>
            <p>lastName: <small>${data.surname}</small> </p>
            <p>Salery: <small>${data.salery}</small> </p>
            <p>Email: <small> ${data.email}</small></p>
            <p>UpdatedAt: <small>${data.time}</small> </p>
            </div>
            <div class="card_process">
              <button class="edit_item_btn" onclick=${editItem(index)} data-id="${index}">Edit</button>
              <button onclick="deleteItem(${index})">Delete</button>
            </div>
          </li>`
    });

    document.getElementById("main__section_container").innerHTML += markupElement;
}



document.onload = showData();



